import MissionHeader from '../mission/header'
import MissionDetail from '../mission/detail'
import axios from 'axios'
export default {
  name: 'header',
  components: {
    MissionHeader,
    MissionDetail
  },
  data () {
    return {
      mission: [],
      name: ''
    }
  },
  methods: {
    getMission () {
      axios.get(this.url + '/mission/id/' + this.$route.params.miId)
        .then(this.getMissionSucc)
    },
    getMissionSucc (res) {
      if (res) {
        const data = res.data
        this.mission = data
        this.name = data.name
        console.log(this.mission)
      }
    }
  },
  created () {
    this.getMission()
  }
}



